import React from 'react'

const DescripCard = ({msg, src, pos}) => {
  return (
    <article className="parent-article flex-row nowrap">
      <div className="flex-row nowrap" style={{maxheight:"6rem", zIndex:"40"}}>
        { pos === 1 ? (<img src={src} alt="" className="info-img"/>) : null }
        <p>{msg}</p>
        { pos === 2 ? (<img src={src} alt="" className="info-img"/>) : null }
      </div>
    </article>
  )
}

export default DescripCard