import {useEffect} from "react"
import {useNavigate} from "react-router-dom"

import DescripCard from "./comp/DescripCard"
import InfoCard from "./comp/InfoCard"
import ArrowButtons from "./comp/ArrowButtons"
import * as THREE from 'three'
//import { useLoader } from '@react-three/fiber'

import PageTitle from "./comp/PageTitle"
import {k3cam} from "./k3/k3cam"
import {makeTown} from "./k3/gloomy"
import arrowkeys1 from './img/arrowkeys1.png'      
import wasdkeys1 from './img/wasdkeys1.png'      
import olkeys1 from './img/olkeys1.png'      
import mouse1 from './img/mouseclickdragleft.png'      
import mouse2 from './img/mouseclickdragright.png'      
import controls1 from './img/controls1.png'      
import controls2 from './img/controls2.png'      
import hazInfo3 from './img/hazwaste8.png'      

const Page2 = () => {

  const navigate = useNavigate()

  useEffect(()=>{
    window.scrollTo(0,0)
  }, [])
  
  useEffect(()=>{

    let scene, renderer, w, h, r
    let container = document.getElementById("three-container")  
    let sizer = document.getElementById("three-sizer")      

    scene = new THREE.Scene();
    renderer = new THREE.WebGLRenderer();
    renderer.domElement.oncontextmenu = function () { return false; }
    //renderer.domElement.class = "three-canvas"
    //renderer.domElement.style.display = "flex"
    container.appendChild(renderer.domElement);

    //create k3 cam
    const k3 = new k3cam();
    k3.lights.init(scene);
    k3.posLocked = false;
    k3.viewLocked = false;
    k3.autofly.init();
    k3.addPos(17, 11, -23);
    k3.setDir(50, 4);

    //resize canvas - camera and renderer must be in place first
    function resizeCanvas(){
      r  = sizer.getBoundingClientRect()
      w = r.width
      h = w/2.0
      if(h < 300) h = 300;
      renderer.setSize(w, h);
      k3.camera.aspect = w/h;				
      k3.camera.updateProjectionMatrix();  
    }
    resizeCanvas()
    window.addEventListener("pageshow", resizeCanvas)
    window.addEventListener("resize", resizeCanvas)

    //add keyboard / mouse listener
    window.addEventListener( 'mousedown', (e)=>k3.mouseDown(e), false );
    window.addEventListener( 'mousemove', (e)=>k3.mouseMove(e), false );
    window.addEventListener( 'mouseup', (e)=>k3.mouseUp(e), false );
    //set capture true for keydown event so it goes to this element first
    window.addEventListener( 'keydown', (e)=>k3.keyDown(e), true );
    //add swipe listener
    let swipeStart1 = document.getElementById("swipe-start1")  
    swipeStart1.addEventListener("touchstart", (e)=>k3.swipeDown1(e), true);
    swipeStart1.addEventListener("touchmove", (e)=>k3.swipeMove1(e), true);
    let swipeStart2 = document.getElementById("swipe-start2")  
    swipeStart2.addEventListener("touchstart", (e)=>k3.swipeDown2(e), true);
    swipeStart2.addEventListener("touchmove", (e)=>k3.swipeMove2(e), true);
    let swipeStart3 = document.getElementById("swipe-start3")  
    swipeStart3.addEventListener("touchstart", (e)=>k3.swipeDown3(e), true);
    swipeStart3.addEventListener("touchmove", (e)=>k3.swipeMove3(e), true);

    function animate() {
      requestAnimationFrame( animate );
      k3.lights.update(k3.camera.position);				
      renderer.render(scene, k3.camera);
    }  

    //load textures, build scene, start animate
    const loadManager = new THREE.LoadingManager();
    const loader = new THREE.TextureLoader(loadManager);
    const tex_sky = [
      loader.load('./k3/skybox/xpos.png'),
      loader.load('./k3/skybox/xneg.png'),
      loader.load('./k3/skybox/ypos.png'),
      loader.load('./k3/skybox/yneg.png'),
      loader.load('./k3/skybox/zpos.png'),
      loader.load('./k3/skybox/zneg.png')
    ]
    const tex_bldg = [
      loader.load('./k3/k3bldg/bldg1.png'),
      loader.load('./k3/k3bldg/bldg10.png'),
      loader.load('./k3/k3bldg/bldg3.png'),
      loader.load('./k3/k3bldg/bldg8.png'),
      loader.load('./k3/k3bldg/bldg9.png'),
      loader.load('./k3/k3bldg/bldg5.png'),
      loader.load('./k3/k3bldg/bldg6.png')
    ]
    const tex_road = loader.load('./k3/road1.png')
    const tex_sidewalk = loader.load('./k3/sidewalk1.png')
  
    loadManager.onLoad = () => {
      tex_bldg[1].wrapS = THREE.RepeatWrapping;		
      tex_road.wrapS = THREE.RepeatWrapping;		
      tex_road.wrapT = THREE.RepeatWrapping;		      
      tex_sidewalk.wrapS = THREE.RepeatWrapping;		
      tex_sidewalk.wrapT = THREE.RepeatWrapping;		
   
      //build scene           
      scene.add(k3.makeSkybox(tex_sky));
      scene.add(k3.autofly.makePath());
      makeTown(scene, tex_bldg, tex_road, tex_sidewalk)      
      animate();
    };  

    return ()=>{ //useEffect
      container.removeChild(renderer.domElement);
      window.removeEventListener("pageshow", resizeCanvas)      
      window.removeEventListener("resize", resizeCanvas)
      window.removeEventListener( 'mousedown', k3.mouseDown);
      window.removeEventListener( 'mousemove', k3.mouseMove);
      window.removeEventListener( 'mouseup', k3.mouseUp);
      window.removeEventListener( 'keydown', k3.keyDown );      
      swipeStart1.removeEventListener("touchstart", k3.swipeDown1);
      swipeStart1.removeEventListener("touchmove", k3.swipeMove1);
      swipeStart2.removeEventListener("touchstart", k3.swipeDown2);
      swipeStart2.removeEventListener("touchmove", k3.swipeMove2);
      swipeStart3.removeEventListener("touchstart", k3.swipeDown3);
      swipeStart3.removeEventListener("touchmove", k3.swipeMove3);
    }

  }, [])

  let str1 = ["The arrow keys change the position of the camera.",
              " If you move the camera forwards or backwards, the camera moves towards or away from where it is looking.",
              " If the camera is pointed upwards or downwards, it will change elevation when it moves forwards or backwards.",
              (<code key="5002" style={{color:"var(--text-green)"}}> Tip: </code>),
              (<code key="5003" style={{color:"var(--text-orange)"}}> If you hold the</code>),
              (<code key="5004" style={{color:"var(--text-green)"}}> up arrow</code>),
              (<code key="5005" style={{color:"var(--text-orange)"}}> while you</code>),
              (<code key="5006" style={{color:"var(--text-green)"}}> right click and drag</code>),
              (<code key="5007" style={{color:"var(--text-orange)"}}> you can fly around.</code>)]

  let str2 = ["The WASD keys change the rotation of the camera.",
              " The forwards and backwards movement of the camera is based on this rotation.",
              " This is similar to right-click and dragging the mouse."]

  let str3 = ["The OL keys raise and lower the camera.",
              " This changes only the elevation of the camera, regardless of the camera rotation.",
              " So if the camera was upside down, it wouldn't make a difference since the elevation is based on the world axis."]

  let str4 = ["Left click and drag the mouse while keeping the button pressed to change the position of the camera.",
              (<code key="5000" style={{color:"var(--text-orange)"}}> Tip:</code>),
              " You can click anywhere on the window, and you can move beyond the boundary of the renderer while dragging the mouse."]              

  let str5 = ["Right click and drag the mouse while keeping the button pressed to change the rotation of the camera.",
              " The camera rotation sets the point where the camera is looking, and when you change the position of the camera it moves relative to this point.",
              (<code key="5001" style={{color:"var(--text-orange)"}}> Tip:</code>),
              " You can click anywhere on the window, and you can move beyond the boundary of the renderer while dragging the mouse."]

  let str6 = ["If you are using a phone, or something that supports touch events in a browser, you can use the swipe controls underneath the renderer.",
              " Begin your swipe from one of these controls to navigate the camera instead of scrolling the screen.",
              " The left control adjusts the camera position, the right control adjusts the rotation, and the middle control adjusts the elevation."]

  let str7 = ["On this page you get full control of the camera navigation. You can change the position, rotation, and elevation of the camera.",
              " Below are instructions for the controls (mouse, keyboard, and touch)."]

              
  return (
    <section className="page-section" data-desc="is-sizer" id="page-sizer" style={{position:"relative"}}>
      <PageTitle title="Gloomy Town" subtitle="User Navigation"/>
      <DescripCard msg={str7} src={hazInfo3} pos={3} />

      <article className="parent-article">
        <div className="child-article three-sizer-container">        
            <div data-desc="is-sizer" id="three-sizer"></div>
        </div>

        <div className="child-article three-canvas-container" id="three-container"></div>
      </article>

      <article className="parent-article flex-row nowrap">
        <img id="swipe-start1" src={controls1} alt="" style={{width:"3rem", padding:"1.5rem", zIndex:80}}/>       
        <img id="swipe-start3" src={controls1} alt="" style={{width:"3rem", padding:"1.5rem", zIndex:80}}/>                
        <img id="swipe-start2" src={controls1} alt="" style={{width:"3rem", padding:"1.5rem", zIndex:80}}/>                
      </article>      
     
      <ArrowButtons name1="Prev Page" fn1={()=>navigate('/page1')}/>

      <article className="parent-article flex-row" style={{alignItems:"stretch"}}>
        <InfoCard infoText={str6} imgSrc={controls2} key="600" />        
        <InfoCard infoText={str4} imgSrc={mouse1} key="601" />      
        <InfoCard infoText={str5} imgSrc={mouse2} key="602"/>
        <InfoCard infoText={str1} imgSrc={arrowkeys1} key="603"/>
        <InfoCard infoText={str2} imgSrc={wasdkeys1} key="604"/>
        <InfoCard infoText={str3} imgSrc={olkeys1} key="605"/>
      </article>
    </section>
  )
}

export default Page2