import {useEffect} from "react"
import {useNavigate} from "react-router-dom"

import * as THREE from 'three'
//import { useLoader } from '@react-three/fiber'

import PageTitle from "./comp/PageTitle"
import DescripCard from "./comp/DescripCard"
import ArrowButtons from "./comp/ArrowButtons"
import hazInfo1 from './img/hazwaste6.png'      
import hazInfo2 from './img/hazwaste8.png'      
import {k3cam} from "./k3/k3cam"
import {makeTown} from "./k3/gloomy"


const Page1 = () => {

  const navigate = useNavigate()

  useEffect(()=>{
    window.scrollTo(0,0)
  }, [])
  
  useEffect(()=>{

    let scene, renderer, cam_rot, w, h, r
    let container = document.getElementById("three-container")  
    let sizer = document.getElementById("three-sizer")      

    scene = new THREE.Scene();
    renderer = new THREE.WebGLRenderer();
    renderer.domElement.oncontextmenu = function () { return false; }
    //renderer.domElement.class = "three-canvas"
    //renderer.domElement.style.display = "flex"
    container.appendChild(renderer.domElement);

    //create k3 cam
    const k3 = new k3cam();
    k3.lights.init(scene);
    k3.posLocked = true;
    k3.viewLocked = true;
    k3.autofly.init();
    k3.addPos(17, 11, -23);
    k3.setDir(50, 4);
    cam_rot = 0.0;

    //resize canvas - camera and renderer must be in place first
    function resizeCanvas(){
      r  = sizer.getBoundingClientRect()
      w = r.width
      h = w/2.0
      if(h < 300) h = 300;      
      renderer.setSize(w, h);
      k3.camera.aspect = w/h;				
      k3.camera.updateProjectionMatrix();  
    }
    resizeCanvas()
    window.addEventListener("pageshow", resizeCanvas)
    window.addEventListener("resize", resizeCanvas)

    function animate() {
      requestAnimationFrame( animate );
      k3.lights.update(k3.camera.position);				
    
      //auto nav - can disable
      k3.autofly.wayscroll += 0.0003
      if(k3.autofly.wayscroll >= 1.0)
        k3.autofly.wayscroll -= 1.0;
      let wayscroll2 = k3.autofly.wayscroll + 0.1;
      if(wayscroll2 >= 1.0)
        wayscroll2 -= 1.0;
      
      let pt = k3.autofly.wayspline2.getPoint(k3.autofly.wayscroll);
      let pt2 = k3.autofly.wayspline2.getPoint(wayscroll2);
      //var pt2 = new THREE.Vector3(0,0,0); //util
        
      k3.camera.position.x = pt.x;
      k3.camera.position.y = pt.y + 5;
      k3.camera.position.z = pt.z;

      k3.lookPoint = pt2;
      k3.camera.lookAt(k3.lookPoint);
      k3.updateDir();					
      
      cam_rot -= 0.001;
      if(cam_rot >= 360.0)
        cam_rot -= 360.0;
      k3.camera.rotation.y += cam_rot;				
      //--

      //end scene
      renderer.render(scene, k3.camera);
    }  

    //add texture loader here
    const loadManager = new THREE.LoadingManager();
    const loader = new THREE.TextureLoader(loadManager);

    const tex_sky = [
      loader.load('./k3/skybox/xpos.png'),
      loader.load('./k3/skybox/xneg.png'),
      loader.load('./k3/skybox/ypos.png'),
      loader.load('./k3/skybox/yneg.png'),
      loader.load('./k3/skybox/zpos.png'),
      loader.load('./k3/skybox/zneg.png')
    ]
    const tex_bldg = [
      loader.load('./k3/k3bldg/bldg1.png'),
      loader.load('./k3/k3bldg/bldg10.png'),
      loader.load('./k3/k3bldg/bldg3.png'),
      loader.load('./k3/k3bldg/bldg8.png'),
      loader.load('./k3/k3bldg/bldg9.png'),
      loader.load('./k3/k3bldg/bldg5.png'),
      loader.load('./k3/k3bldg/bldg6.png')
    ]
    const tex_road = loader.load('./k3/road1.png')
    const tex_sidewalk = loader.load('./k3/sidewalk1.png')
  
    loadManager.onLoad = () => {

      tex_bldg[1].wrapS = THREE.RepeatWrapping;		
      tex_road.wrapS = THREE.RepeatWrapping;		
      tex_road.wrapT = THREE.RepeatWrapping;		      
      tex_sidewalk.wrapS = THREE.RepeatWrapping;		
      tex_sidewalk.wrapT = THREE.RepeatWrapping;		
    
      //build scene      
      scene.add(k3.makeSkybox(tex_sky));
      makeTown(scene, tex_bldg, tex_road, tex_sidewalk)      
      animate();
    };  

    return ()=>{
      container.removeChild(renderer.domElement);
      window.removeEventListener("pageshow", resizeCanvas)      
      window.removeEventListener("resize", resizeCanvas)
    }

  }, [])

  let str1 = ["This demo uses ",
              (<code key="6001" style={{color:"var(--text-green)"}}>Three.js</code>),
              " inside of ",
              (<code key="6002" style={{color:"var(--text-orange)"}}>React</code>),
              " to render a continous 3D fly through of a gloomy little town.",
              " The camera is my custom object boasting smooth navigation, keyboard interaction, auto-fly nav, a 2D overlay, an attached skybox, and lights that follow it around.",
              " The cam is easy to setup, and as a bonus the code is tried, tested and true which is significant if you've ever had to write any cam nav code.",
              " Also significant is having this run inside of ",
              (<code key="6003" style={{color:"var(--text-orange)"}}>React</code>),
              " and contained in a responsive layout.",
              " I should probably mention the dust effect that overlays the main body of the page. This isn't ",
              (<code key="6004" style={{color:"var(--text-green)"}}>Three.js</code>),
              " , its an animated background component that I wrote which does different things from bubbles to starfields.",
              " I added it here to make things feel more irradiated."]
  
  let str2 = ["The flight path is a series of bezier curves forming a circuit which is subsequently segmented then interpolated for flight.",
              " This page features an auto-fly cam but on the next page you can interact with the camera and control its position, rotation and elevation.",
              " This allows you to see how the demo was put together, including the camera navigation circuit, ",
              " and it gives you a better look at the creepy skybox."]

  return (
    <section className="page-section">
      <PageTitle title="Gloomy Town" subtitle="Auto-fly"/>
      <DescripCard msg={str1} src={hazInfo1} pos={1} />

      <article className="parent-article">
        <div className="child-article three-sizer-container">        
            <div data-desc="is-sizer" id="three-sizer"></div>
        </div>
        <div className="child-article three-canvas-container" id="three-container"></div>
      </article>

      <ArrowButtons name2="Next Page" fn2={()=>navigate('/page2')}/>
      <DescripCard msg={str2} src={hazInfo2} pos={2} />
    </section>
  )
}

export default Page1