	function doConvert(	g1, g2, g3,	g1_vertices, g2_vertices, g3_vertices,
											g1_faceVertexUvs, g2_faceVertexUvs, g3_faceVertexUvs){
		//-- start conversion code
		let g1p = [g1_vertices[0].x, g1_vertices[0].y, g1_vertices[0].z,
							 g1_vertices[1].x, g1_vertices[1].y, g1_vertices[1].z,							 		
							 g1_vertices[2].x, g1_vertices[2].y, g1_vertices[2].z,
							 g1_vertices[3].x, g1_vertices[3].y, g1_vertices[3].z]
		g1.setAttribute( 'position', new THREE.Float32BufferAttribute( g1p, 3 ) );				

		let g2p = [g2_vertices[0].x, g2_vertices[0].y, g2_vertices[0].z,
							 g2_vertices[1].x, g2_vertices[1].y, g2_vertices[1].z,
							 g2_vertices[2].x, g2_vertices[2].y, g2_vertices[2].z,
							 g2_vertices[3].x, g2_vertices[3].y, g2_vertices[3].z]
		g2.setAttribute( 'position', new THREE.Float32BufferAttribute( g2p, 3 ) );				

		let g3p = [g3_vertices[0].x, g3_vertices[0].y, g3_vertices[0].z,
							 g3_vertices[1].x, g3_vertices[1].y, g3_vertices[1].z,							 
							 g3_vertices[2].x, g3_vertices[2].y, g3_vertices[2].z,
							 g3_vertices[3].x, g3_vertices[3].y, g3_vertices[3].z]
		g3.setAttribute( 'position', new THREE.Float32BufferAttribute( g3p, 3 ) );				
		
		var g1_uv = [	g1_faceVertexUvs[0][0][0].x, g1_faceVertexUvs[0][0][0].y,
									g1_faceVertexUvs[0][0][2].x, g1_faceVertexUvs[0][0][2].y,
									g1_faceVertexUvs[0][1][0].x, g1_faceVertexUvs[0][1][0].y,
									g1_faceVertexUvs[0][1][1].x, g1_faceVertexUvs[0][1][1].y ];
		g1.setAttribute( 'uv', new THREE.Float32BufferAttribute( g1_uv, 2 ) );
		
		var g2_uv =	[	g2_faceVertexUvs[0][0][0].x, g2_faceVertexUvs[0][0][0].y,
									g2_faceVertexUvs[0][0][2].x, g2_faceVertexUvs[0][0][2].y,
									g2_faceVertexUvs[0][1][0].x, g2_faceVertexUvs[0][1][0].y,
									g2_faceVertexUvs[0][1][1].x, g2_faceVertexUvs[0][1][1].y ];
		g2.setAttribute( 'uv', new THREE.Float32BufferAttribute( g2_uv, 2 ) );

		var g3_uv = [	g3_faceVertexUvs[0][0][0].x, g3_faceVertexUvs[0][0][0].y,
									g3_faceVertexUvs[0][0][2].x, g3_faceVertexUvs[0][0][2].y,
									g3_faceVertexUvs[0][1][0].x, g3_faceVertexUvs[0][1][0].y,
									g3_faceVertexUvs[0][1][1].x, g3_faceVertexUvs[0][1][1].y ];
		g3.setAttribute( 'uv', new THREE.Float32BufferAttribute( g3_uv, 2 ) );
		//-- end conversion code
	}
