import React from 'react'

const InfoCard = ( {infoText, imgSrc}) => {

  return (
     <article className="parent-article info-card flex-col">
      <div className="flex-col" style={{zIndex:"40"}}>
        <img src={imgSrc} alt="" className="info-img"/>
        <p>{infoText}</p>       
      </div>
    </article>
  )
}

export default InfoCard