//gloomy.js - custom file for demo, builds the town

import * as THREE from 'three'
import {k3bldg} from "./k3bldg"

export function makeTown(scene, tex_bldg, tex_road, tex_sidewalk){
  
  scene.add(makeRoad(tex_road)); //make first
  scene.add(makeSidewalks(tex_sidewalk))
  makeBuildings(scene, tex_bldg)
}

function makeSidewalks( tex ){
  let b1 = new THREE.Object3D();
  b1.add(makeSidewalk(-71, -3, 106, 173, tex));
  b1.add(makeSidewalk(55, -3, 50, 173, tex));
  b1.add(makeSidewalk(125, -3, 50, 60, tex));
  b1.add(makeSidewalk(125, 67, 50, 103, tex));
  b1.add(makeSidewalk(125, -111, 50, 88, tex));
  b1.add(makeSidewalk(55, -111, 50, 88, tex));
  b1.add(makeSidewalk(-3, -147, 38, 124, tex));
  b1.add(makeSidewalk(-48, -147, 38, 124, tex));
  b1.add(makeSidewalk(-128, -3, 38, 173, tex));
  b1.add(makeSidewalk(55, -203, 120, 74, tex));
  return b1;
}

function makeBuildings(scene, tex){
  const bldg_mkr = new k3bldg(tex, true);
  makeArea1(scene, bldg_mkr);
  makeArea2(scene, bldg_mkr);
  makeArea3(scene, bldg_mkr);
  makeArea4(scene, bldg_mkr);
  makeArea5(scene, bldg_mkr);
  makeArea6(scene, bldg_mkr);
  makeArea7(scene, bldg_mkr );
  makeArea8(scene, bldg_mkr );
  makeArea9(scene, bldg_mkr);
  makeArea10(scene, bldg_mkr );				
}

export function makeSidewalk(px, pz, dx, dz, tex){

  let g1 = new THREE.PlaneGeometry(dx, dz);

  let uvs = [ 0.0, dz*0.05,  dx*0.025, dz*0.05,  0.0, 0.0, dx*0.025, 0.0 ];
  g1.setAttribute('uv', new THREE.Float32BufferAttribute(uvs, 2));

  let m1 = new THREE.MeshBasicMaterial({map: tex});
  m1.color.setRGB(0.45, 0.5, 0.5);

  let p1 = new THREE.Mesh( g1, m1 );		
  p1.position.x = px + (dx*0.5);
  p1.position.y = 0;
  p1.position.z = pz + (dz*0.5);
  p1.rotation.x = -0.5*Math.PI;
  return p1;
}

function makeRoad( tex ){

  let g1 = new THREE.PlaneGeometry( 400, 400 );

  let uvs = [ 0.0, 50,  50, 50,  0.0, 0.0, 50, 0.0 ];
  g1.setAttribute('uv', new THREE.Float32BufferAttribute(uvs, 2));
  
  let m1 = new THREE.MeshBasicMaterial( {map: tex} );
  let p1 = new THREE.Mesh( g1, m1 );		
  p1.position.y = -0.002;
  p1.rotation.x = -0.5*Math.PI;
  m1.depthWrite = false;
  return p1;		
}

